import expr, {
  AdvancedBranchingLogicQuestionType,
  ResponseContactType,
  QuestionResponseAnswerType,
  ResponseContext,
  ResponseContextSurvey,
  ResponseContextCustomDataKeys,
  QuestionResponsesType,
} from ".";

const responseDataHygiene = (
  data: QuestionResponsesType[]
): QuestionResponsesType[] => {
  const transformedResponses = [];

  for (const response of data) {
    const newResponse = { ...response };
    const { type, subtype, answers } = newResponse;

    if (type === 'matrix') {
      if (subtype === 'rating') {
        let newResponseAnswer: QuestionResponseAnswerType = { answer_id: '' };

        for (const answer of answers) {
          const { answer_id, ...otherProps } = answer;

          if (answer_id === '0') {
            newResponseAnswer = { ...newResponseAnswer, ...otherProps };
          } else {
            newResponseAnswer = { ...newResponseAnswer, ...answer };
          }
        }

        newResponse.answers = [newResponseAnswer];
      }
    }

    transformedResponses.push(newResponse);
  }

  return transformedResponses;
};

const createResponseContext = (
  contactData: ResponseContactType,
  questions: AdvancedBranchingLogicQuestionType[],
  responseData: QuestionResponsesType[],
  customData: unknown[] = [],
  surveyLanguage = 'en',
  warningOnMissingResponse = false
): ResponseContext => {
  const survey: ResponseContextSurvey = {
    questions,
    language: surveyLanguage,
    getQuestion: (id: string) => {
      const question = questions.find(q => q.id === id);

      return question
        ? ({
            answer_options: { rows: [] },
            ...question,
          } as AdvancedBranchingLogicQuestionType)
        : null;
    },
  };

  // Fun Fact: Custom data can be 1-250 items. See L#628 in responseweb/models/advanced_logic.py
  const mappedCustomData = customData
    .slice(0, 250)
    .reduce((state: object, data: unknown, index) => {
      return { ...state, [`custom${index + 1}`]: data };
    }, {}) as Record<ResponseContextCustomDataKeys, unknown>;

  return new expr.ResponseContext(
    responseDataHygiene(responseData),
    survey,
    {
      contact: contactData,
      custom: mappedCustomData,
    },
    warningOnMissingResponse
  );
};

export default createResponseContext;
