const nodes = require('../lib/nodes');

const Op = nodes.Op;

const arithOp = (leftSide, rightSide, op) => {
  const leftIsArray = Array.isArray(leftSide);
  const rightIsArray = Array.isArray(rightSide);

  const leftArr = leftIsArray ? leftSide : [leftSide];
  const rightArr = rightIsArray ? rightSide : [rightSide];

  // config array default
  if (leftArr.length === 0) {
    leftArr.push(0);
  }

  if (rightArr.length === 0) {
    rightArr.push(0);
  }

  const leftIsLonger = leftArr.length > rightArr.length;
  const arithOpResult = [];

  // NOTE: could add recursion here to make the X number of sub arrays on the left or right side be evaluated.

  leftArr.forEach((leftNum, leftArrayIndex) => {
    rightArr.forEach((rightNum, rightArrayIndex) => {
      // determine which index to use
      const index = leftIsLonger ? leftArrayIndex : rightArrayIndex;

      const left = Number(leftNum);
      const right = Number(rightNum);

      if (Number.isNaN(left) || Number.isNaN(right)) {
        arithOpResult[index] = null;
        return;
      }

      let result;
      switch (op) {
        case Op.Add:
          result = left + right;
          break;
        case Op.Sub:
          result = left - right;
          break;
        case Op.Mul:
          result = left * right;
          break;
        case Op.Div:
          result = left / right;
          break;
      }

      arithOpResult[index] = result;
    });
  });

  return arithOpResult.length === 1 ? arithOpResult[0] : arithOpResult;
};

module.exports = arithOp;
