const Diacritics = require('modern-diacritics'); // Because of the monorepo strategy, Jest needs this module name mapped. See moduleNameMapper section in 'packages/survey-logic/jest.config.js'

function hasDiacritic(str) {
  const subject = `${str}`.normalize('NFD');
  let result = false;
  try {
    // more complete modern variant
    result = /\p{Diacritic}/gu.test(subject);
  } catch (_a) {
    // backwards compatible variant
    result = /[\u0300-\u036f]/g.test(subject);
  }

  return result;
}

function getLeftRightString(left, right, rightStrict = false) {
  const options = { lowerCase: true };

  const a = hasDiacritic(left);
  const b = hasDiacritic(right);

  const lower_left = `${left ?? ''}`.toLowerCase();
  const lower_right = `${right ?? ''}`.toLowerCase();

  if (a && b) {
    return [lower_left, lower_right];
  }

  return [
    Diacritics.latinize(left, options),
    rightStrict ? right : Diacritics.latinize(right, options),
  ];
}

exports.latinize = Diacritics.latinize;
exports.normalizeSymbols = Diacritics.normalizeSymbols;
exports.removeDiacritics = Diacritics.removeDiacritics;
exports.hasDiacritic = hasDiacritic;
exports.getLeftRightString = getLeftRightString;
