import ExprCore from '@sm/smlib.expr.core'
import { sample1 as surveyQuestions } from "../../src/app/sample-data/questions";
import { sample1 as responseData } from '../../src/app/sample-data/responses';
import { contactAndQuestion } from "../../src/app/contactAndQuestion";

const conditions = contactAndQuestion.map((item) => item.condition)


self.onmessage = (e) => {
    console.log('Message received from main script', e);
    console.log('Expr core', ExprCore)

    const parsedConditions = ExprCore.load(conditions)

    const responseContext = ExprCore.createResponseContext({}, surveyQuestions as never, responseData)
    // console.log(parsedConditions)
    // console.log(responseContext)

    console.log(ExprCore.evaluate(parsedConditions, responseContext))
};